import {Component, OnInit, Input} from '@angular/core';
import {Router} from "@angular/router";

@Component({
  selector: 'page-indicator',
  templateUrl: './page-indicator.component.html',
  styleUrls: ['./page-indicator.component.css']
})
export class PageIndicatorComponent implements OnInit {

  constructor(private router: Router) {
  }

  @Input() links: Array<string> = [];
  itemsArray: Array<any> = [];

  ngOnInit() {
    const n = this.links.length;
    for (let i: number = 0; i < n; i++) {
      if (this.router.url === this.links[i]) {
        this.itemsArray.push({
          code: "&#8226;",
          className: "black",
          link: this.links[i]
        });
      } else if (i === n - 1) {
        this.itemsArray.push({
          code: "&#9702;",
          className: "clear",
          link: this.links[i]
        });
      } else {
        this.itemsArray.push({
          code: "&#8226;",
          className: "gray",
          link: this.links[i]
        });
      }
    }
  }

}
